import React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Grid from '@mui/material/Grid';
import axios from "../axios";
import {useDispatch, useSelector} from "react-redux";
import { Post } from '../components/Post';
import { TagsBlock } from '../components/TagsBlock';
import { CommentsBlock } from '../components/CommentsBlock';
import {fetchMedicalNotes} from "../redux/slices/medicalNotes";

export const Home = () => {
    const dispatch = useDispatch();
    const {medicalnotes, tags}= useSelector(state => state.medicalnotes);
    const isMedicalNotesLoading = medicalnotes.status === 'loading';
    React.useEffect(() =>{
        dispatch(fetchMedicalNotes());
    }, []);

  return (
    <>
      <Tabs style={{ marginBottom: 15 }} value={0} aria-label="basic tabs example">
        <Tab label="" />
        <Tab label="" />
      </Tabs>
      <Grid container spacing={4}>
        <Grid xs={8} item>
          {(isMedicalNotesLoading ?[...Array(5)]: medicalnotes.items).map((obj, index) => isMedicalNotesLoading ?
              <Post key ={index} isLoading={true}/>:(
                  <Post
                      id={obj._id}
                      title= {obj.title}
                      imageUrl="/public/logo.png"
                      user={obj.doctor}
                      createdAt={obj.createdAt}
                      tags={['react', 'fun', 'typescript']}
                      isEditable
                  />
              ),
          )}
        </Grid>
        <Grid xs={4} item>
          <TagsBlock items={['react', 'typescript', '']} isLoading={false} />
          <CommentsBlock
            items={[
              {
                user: {
                  fullName: ' ',
                  avatarUrl: 'https://mui.com/static/images/avatar/1.jpg',
                },
                text: '  ',
              },
              {
                user: {
                  fullName: ' ',
                  avatarUrl: 'https://mui.com/static/images/avatar/2.jpg',
                },
                text: 'When displaying three lines or more, the avatar is not aligned at the top. You should set the prop to align the avatar at the top',
              },
            ]}
            isLoading={false}
          />
        </Grid>
      </Grid>
    </>
  );
};
